footer {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 100%;
    min-height: 150px;
    margin-top: 30px;
    background-color: rgb(24, 24, 24);
    .content {
        display: flex;
        padding: 20px;
    }
    .first--content, .second--content {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        min-width: 200px;
        max-width: auto;
        color:rgb(236, 236, 236);
        img {
            background-image: url("https://static.thenounproject.com/png/756729-200.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            width: 100px;
            height: 100px;
        }
    }
    .third--content {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
        color:white;
        a {
            max-width: 100%;
            white-space: nowrap;
            overflow: hidden;
            white-space: initial;
            color: rgb(79, 79, 206);
        }
        @media (max-width: 636px) {
            background-color: #222;
        }
    }
}