:root {
    --box-day: rgb(202, 202, 202);
    --box-day-text: black;
    --box-hour: rgb(58, 58, 58);
    --schedule-program: #86868654;
    --box-width: 9rem;
    --box-height: 1.3rem;
}
.schedule-container {
    display: grid;
    margin: 20px;
    width: auto;
    max-width: calc(3px + 8 * var(--box-width));
    grid-template-columns: var(--box-width) auto;
    grid-gap: 0px;
    grid-auto-rows: var(--box-height) calc(100% - var(--box-height));
    //border: black solid 0.5px;
    overflow: auto;
    .schedule_days-container {
        grid-column: 1 / 3;
        grid-row: 1;
        display: flex;
        .box-day {
            display: flex;
            align-items: center;
            justify-content: center;
            width: var(--box-width);
            height: var(--box-height);
            font-size: 0.8rem;
            text-align: center;
            border-width: 1px 0 1px 1px;
            border-color: black;
            border-style: solid;
            color: var(--box-day-text);
            background-color: var(--box-day);
        }
    }
    .schedule_hours-continer {
        grid-column: 1;
        grid-row: 2;
        .box-hour {
            display: flex;
            align-items: center;
            justify-content: center;
            width: var(--box-width);
            height: var(--box-height);
            font-size: 0.6rem;
            text-align: center;
            border-width: 0 1px 1px 1px;
            border-color: black;
            border-style: solid;
            background-color: var(--box-hour);
        }
    }
    .schedule_program-container {
        grid-column: 2;
        grid-row: 2;
    
        display: grid;
        grid-template-columns: repeat(7, var(--box-width));
        grid-gap: 0px;
        grid-template-rows: repeat(24, var(--box-height));
        background-color: var(--schedule-program);
        border: 0.5px black;
        border-style: none solid none none;
        .box-program {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin: 0;
            padding: 0 2px;
            width: var(--box-width);
            height: auto;
            text-align: center;
            //border: 1px black solid;
            cursor: pointer;
            p {
                margin: 0;
                padding: 0;
                max-width: 100%;
                overflow: hidden;
		        mix-blend-mode: exclusion;
                font-size: 0.8rem;
            }
            
        }
        #p1 {
            grid-column: 2;
            grid-row: 1 / 4;
            background-color: violet;
        }
    }
    
}