:root {
    --bg-form: rgb(58, 58, 58);
}

form {
    color: white;
    .form--program__container {
        .form--title {
            h1 {
                margin: 0;
            }
        }
        .form--body {
            margin-top: 20px;
            div {
                input, select {
                    color: rgb(92, 92, 92);
                }
                .announcers {
                    display: flex;
                    justify-content: space-around;
                }
            }
        }
        .form--footer {
            display: flex;
            justify-content: space-around;
            margin-top: 30px;
        }
    }
}